  function numberToUAText(amount) {
    const ones = [
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
    ];
    const teens = [
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
    ];
    const tens = [
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
    ];
    const hundreds = [
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
    ];
    const thousandsForms = ["", "", ""];

    function getForm(number, forms) {
      const n = Math.abs(number) % 100;
      const n1 = n % 10;
      if (n > 10 && n < 20) return forms[2];
      if (n1 > 1 && n1 < 5) return forms[1];
      if (n1 === 1) return forms[0];
      return forms[2];
    }

    function convertTriad(num, feminine = false) {
      let text = "";
      const h = Math.floor(num / 100);
      const t = Math.floor((num % 100) / 10);
      const o = num % 10;
      text += hundreds[h] ? hundreds[h] + " " : "";
      if (t > 1) {
        text += tens[t] + " ";
        text += ones[o] ? ones[o] + " " : "";
      } else if (t === 1) {
        text += teens[o] + " ";
      } else {
        text += ones[o] ? ones[o] + " " : "";
      }
      if (feminine) {
        text = text.replace(" ", " ").replace(" ", " ");
      }
      return text.trim();
    }

    const parts = amount.toFixed(2).split(".");
    const hryvnia = parseInt(parts[0], 10);
    const kopiyky = parts[1];

    let text = "";

    if (hryvnia === 0) {
      text = " ";
    } else {
      const thousands = Math.floor(hryvnia / 1000);
      const remainder = hryvnia % 1000;

      if (thousands > 0) {
        text +=
          convertTriad(thousands, true) +
          " " +
          getForm(thousands, thousandsForms) +
          " ";
      }
      text +=
        convertTriad(remainder) +
        " " +
        getForm(remainder, ["", "", ""]);
    }

    return (
      `${amount.toFixed(2)} . ` +
      `${kopiyky} . ` +
      `(${text.charAt(0).toUpperCase() + text.slice(1)} ${kopiyky} )`
    );
  }

